				"html": "<h2>Introduction</h2>\n<p>In computer programming, a <strong>block</strong> or code <strong>block</strong> is a section of code which is grouped together. Blocks consist of one or more declarations and statements. A programming language that permits the creation of blocks, including blocks nested within other blocks, is called a block-structured programming language. JavaScript is one such programming language.</p>\n<p>A <strong>block</strong> statement in JavaScript is used to group zero or more statements. The block is delimited by a pair of curly brackets.</p>\n<pre><code>{\n  statement_1;\n  statement_2;\n  ...\n  statement_n;\n}\n</code></pre>\n<p><a href=\"https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Statements/block\">MDN link</a></p>\n<h2>Examples</h2>\n<p>The <strong>block</strong> statement is commonly used with control flow statements (e.g. <code>if...else</code>, <code>for</code>, <code>while</code>) and functions.</p>\n<pre><code>while (x &#x3C; 10) {\n  x++;\n}\n\nfunction addnums(num1, num2) {\n  var sum = 0;\n  sum = num1 + num2;\n  return sum;\n}\n</code></pre>",
